:root {
  /* CSS variables for theming */
  --secondary-dark-color: rgba(98, 144, 195, 1);
  --secondary-light-color: rgba(194, 231, 218, 1);
  --primary-light-color:  white;
  --primary-dark-color: rgba(26, 27, 65, 1);
  --primary-highlight-color: rgba(186, 242, 187, 1);

  --positive-color: rgba(186, 242, 18, 1);
  --negative-color: darkred;

  --spacing-md: 1rem;
  --spacing-sm: 0.5rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
  background: var(--primary-light-color);
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0.5rem;
  margin: var(--spacing-sm);
}

.CinemaLayout {
  display: grid;
  grid-template-columns: repeat(5, minmax(50px, 1fr));
  grid-gap: var(--spacing-md);
}

.Seat {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: var(--secondary-light-color);
  padding: var(--spacing-sm);
  -webkit-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
  overflow: hidden;
  border: 0.5rem solid transparent;
  color: var(--primary-light-color);
}

.Seat:not(.Seat-disabled):hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: var(--primary-highlight-color);
}

.Seat-disabled {
  background-color: var(--primary-dark-color);
}

.Seat-selected {
  border-color: var(--positive-color);
}

.SeatIcon {
  position: absolute;
  color: var(--primary-light-color);
}

.Price {
  font-weight: 700;
  padding: var(--spacing-sm);
  background: var(--secondary-dark-color);
}

.SeatNumber {
  font-weight: 700;
  padding: var(--spacing-sm);
}

.Modal {
  display: none;
}

.Modal-open {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.6);
}

.ModalContent {
  position: relative;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: var(--primary-light-color);
  max-width: 90vw;
  padding-bottom: var(--spacing-md);
  z-index: 10;
}

.ModalCloseIcon {
  cursor: pointer;
  position: absolute;
  color: var(--primary-dark-color);
  right: 0;
  margin-top: var(--spacing-sm);
  margin-right: var(--spacing-sm);
  top: 0;
}

.Ticket {
  position: relative;
  background: var(--secondary-dark-color);
  padding: var(--spacing-md);
  min-height: 150px;
  width: 100%;
  max-width: 500px;
  border-radius: 20px;
}

.TicketLeftEdge {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: -5px;
}

.LeftArrow {
	border-color: transparent var(--secondary-dark-color);
	border-style: solid;
	border-width: 10px 10px 10px 0px;
}

.TicketRightEdge {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: -5px;
}

.RightArrow {
	border-color: transparent var(--secondary-dark-color);
	border-style: solid;
	border-width: 10px 0px 10px 10px;
}

.TicketContent {
  border: 10px solid var(--secondary-light-color);
  border-radius: 20px;
  padding: var(--spacing-sm);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: 700;
  font-size: 2rem;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Button {
  box-shadow: none;
  border: none;
  padding: var(--spacing-md);
  font-weight: 700;
  font-size: 1.5rem;
  cursor: pointer;
  border-bottom: 5px solid transparent;
  -webkit-transition: 0.2ms ease-out;
  transition: 0.2ms ease-out;
}

.Button:hover {
  border-color: black;
}

.Button-positive {
  background: var(--positive-color);
}

.Button-negative {
  background: var(--negative-color);
}

.Booking {
  text-align: center;
  padding-left: var(--spacing-md);
  padding-right: var(--spacing-md);
}

.FlexColumn {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.FlexColumn > :not(:last-child) {
  margin-bottom: var(--spacing-sm);
}

.LegendContainer {
  width: 400px;
  padding: var(--spacing-md);
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.LegendContainer > div {
  display: -webkit-flex;
  display: flex;
}

.Legend {
  display: inline-block;
  height: 20px;
  width: 20px;
  margin-right: var(--spacing-sm);
}
.Legend-available {
  background: var(--secondary-light-color);
}
.Legend-unavailable {
  background: var(--primary-dark-color);
}

.CinemaContainer {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding-bottom: var(--spacing-md);
}


